/*
* author: ZJJ
* description: switch 
* :
* @checkedInside: ko Boolean ()
* @disabled: ko Boolean 
* @size: String:large, small (, )
* @checkedValue: 
* @unCheckedValue: 
* @checked: ()
* */
import template from './index.html'
import ko from 'knockout'
function init (params) {
  const PREFIX = 'y-switch-' // 
  this.disabled = params.disabled ? params.disabled : ko.observable(false)
  this.checkedValue = params.checkedValue ? params.checkedValue : true
  this.unCheckedValue = params.unCheckedValue ? params.unCheckedValue : false
  this.checked = params.checked || ko.observable(false) // ko Boolean 
  this.checkedInside = ko.observable()
  this.checkedInside.subscribe((val) => {
    if (val) {
      this.checked(this.checkedValue)
    } else {
      this.checked(this.unCheckedValue)
    }
  })
  this.checked.subscribe((val) => {
    if (val === this.checkedValue) {
      this.checkedInside(true)
    }
    if (val === this.unCheckedValue) {
      this.checkedInside(false)
    }
  })
  this.size = params.size ? (PREFIX + params.size) : '' //   String 
  this.sizeClass = ko.computed(() => {
    return this.disabled() ? this.checkedInside() ? `${this.size} ${PREFIX}checked ${PREFIX}disabled` : `${this.size} ${PREFIX}disabled` : this.checkedInside() ? `${this.size} ${PREFIX}checked` : `${this.size} ${PREFIX}`
  }, this)
  // checked
  if (this.checked() === this.checkedValue) {
    this.checkedInside(true)
  } else {
    this.checkedInside(false)
  }
}
// 
init.prototype.checkSelect = function () {
  if (this.disabled()) {
    return false
  } else {
    this.checkedInside(!this.checkedInside())
  }
}

export default {
  name: 'switch',
  init,
  template
}



